@font-face {
  font-family: "Avenir";
  src: url(/static/media/Avenir-Medium.0fe148f8.ttf) format("truetype");
}

@font-face {
  font-family: "Anonymous Pro";
  src: url(/static/media/Anonymous-Pro-Regular.1a728ab6.ttf) format("truetype");
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: "Avenir", sans-serif;
  font-weight: 900;
  font-size: 32px;
  margin: 0px 0px 6px 0px;
}

h2 {
  font-family: "Avenir", sans-serif;
  font-weight: 100;
  font-size: 18px;
  font-weight: light;
  margin: 0px 0px 6px 0px;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version supported by Chrome and Opera */
}

.input-bracket {
  font-family: "Anonymous Pro", monospace;
  font-weight: 900;
  font-size: 92px;
}

a.light-mode-link:link {
  color: #0047c6;
  text-decoration: none;
}
a.light-mode-link:visited {
  color: #0047c6;
  text-decoration: none;
}
a.light-mode-link:hover {
  color: #65baee;
}

a.dark-mode-link:link {
  color: #2a7fde;
  text-decoration: none;
}
a.dark-mode-link:visited {
  color: #2a7fde;
  text-decoration: none;
}
a.dark-mode-link:hover {
  color: #65baee;
}

#name-title {
  font-family: "Avenir", sans-serif;
  font-weight: 900;
  font-size: 24px;
  margin: 0px 0px 0px 7px;
}

#tagline {
  font-family: "Avenir", sans-serif;
  font-weight: 100;
  font-size: 24px;
  margin: 0px 0px 0px 7px;
}

#top-panel {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 20px;
}

#content-header {
  position: fixed;
  top: 200px;
  left: 0px;
  width: 270px;
  text-align: right;
}

#content-body {
  position: absolute;
  overflow: scroll;
  font-family: "Avenir", sans-serif;
  font-weight: 400;
  margin: 185px 170px 30px 290px;
}

#cmd-list {
  z-index: 2;
  position: fixed;
  right: 72px;
  width: 200px;
  border: 1px solid;
  text-align: center;
  font-family: "Anonymous Pro", monospace;
  font-weight: 400;
  font-size: 14px;
}

#cmd-list > p {
  margin: 2px;
}

#logo {
  z-index: 2;
  position: fixed;
  top: 5px;
  right: 50px;
}

#credits {
  z-index: 2;
  position: fixed;
  bottom: 0px;
  left: 28px;
  padding: 2px 2px 10px 0px;
  font-family: "Avenir", sans-serif;
  font-weight: 100;
  font-size: 14px;
}

.pulse {
  background: -webkit-linear-gradient(225deg, #e5fdff, #002da4);
  background: linear-gradient(225deg, #e5fdff, #002da4);
  background-size: 400% 400%;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: CoolGradient 2s ease infinite;
  -webkit-animation: CoolGradient 2s ease infinite;
  -moz-animation: CoolGradient 2s ease infinite;
}

@keyframes CoolGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-webkit-keyframes CoolGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

